:root {
  --font-primary: "Lexend Deca", sans-serif;
  --font-secondary: "Big Shoulders Display", cursive;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --brightorange: hsl(31, 77%, 52%);
  --darkcyan: hsl(184, 100%, 22%);
  --verydarkcyan: hsl(179, 100%, 13%);
  --transparentwhite: hsla(0, 0%, 100%, 0.75);
  --verylightgray: hsl(0, 0%, 95%);
}

html {
  font-size: 15px 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background-color: var(--white);
  font-family: var(--font-primary);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 8rem auto 4rem auto;
  padding: 2rem;
  max-width: 90rem;
}

.card {
  display: grid;
  grid-template-columns: repeat(3, 310px);
  grid-template-rows: 510px;
  justify-content: center;
}
@media (max-width: 70.3125em) {
  .card {
    grid-template-columns: 704px;
    grid-template-rows: repeat(3, 470px);
  }
}
@media (max-width: 44em) {
  .card {
    grid-template-columns: 310px;
    grid-template-rows: repeat(3, 470px);
  }
}
.card h1 {
  font-family: var(--font-secondary);
  margin: 2rem 0;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--verylightgray);
}
.card p {
  color: var(--transparentwhite);
  font-size: 0.95rem;
  line-height: 2;
  margin: 0;
}
.card-sedan {
  background-color: var(--brightorange);
  padding: 3rem;
  border-radius: 10px 0 0 10px;
}
.card-sedan button {
  background-color: var(--verylightgray);
  border: none;
  border-radius: 25px;
  padding: 0.8rem 2rem;
  margin-top: 5rem;
  color: var(--brightorange);
  font-weight: 600;
}
@media (max-width: 70.3125em) {
  .card-sedan button {
    margin-top: 8.5rem;
  }
}
@media (max-width: 44em) {
  .card-sedan button {
    margin-top: 3rem;
  }
}
.card-sedan button:hover {
  background-color: transparent;
  color: var(--verylightgray);
  border: solid 2px var(--verylightgray);
  cursor: pointer;
}
@media (max-width: 70.3125em) {
  .card-sedan {
    border-radius: 10px 10px 0 0;
  }
}
.card-suv {
  background-color: var(--darkcyan);
  padding: 3rem;
}
.card-suv button {
  background-color: var(--verylightgray);
  border: none;
  border-radius: 25px;
  padding: 0.8rem 2rem;
  margin-top: 5rem;
  color: var(--darkcyan);
  font-weight: 600;
}
@media (max-width: 70.3125em) {
  .card-suv button {
    margin-top: 8.5rem;
  }
}
@media (max-width: 44em) {
  .card-suv button {
    margin-top: 3rem;
  }
}
.card-suv button:hover {
  background-color: transparent;
  color: var(--verylightgray);
  border: solid 2px var(--verylightgray);
  cursor: pointer;
}
.card-luxury {
  background-color: var(--verydarkcyan);
  padding: 3rem;
  border-radius: 0 10px 10px 0;
}
.card-luxury button {
  background-color: var(--verylightgray);
  border: none;
  border-radius: 25px;
  padding: 0.8rem 2rem;
  margin-top: 5rem;
  color: var(--verydarkcyan);
  font-weight: 600;
}
@media (max-width: 70.3125em) {
  .card-luxury button {
    margin-top: 8.5rem;
  }
}
@media (max-width: 44em) {
  .card-luxury button {
    margin-top: 3rem;
  }
}
.card-luxury button:hover {
  background-color: transparent;
  color: var(--verylightgray);
  border: solid 2px var(--verylightgray);
  cursor: pointer;
}
@media (max-width: 70.3125em) {
  .card-luxury {
    border-radius: 0 0 10px 10px;
  }
}

.attribution {
  margin: 4rem auto;
  text-align: center;
}